.nav-bar {
  width: 100%;
  min-height: 60px;
  background-color: black;
  color: white;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 8px 16px;
  gap: 12px;
  flex-wrap: wrap;
  position: relative;
}

.nav-bar .nav-logo {
  display: flex;
  align-items: center;
  gap: 8px;
  flex-shrink: 0;
}

.nav-bar .nav-logo img {
  width: 60px;
  height: auto;
}

.nav-bar .nav-logo p {
  margin: 0;
  padding: 0;
  font-size: 18px;
  font-weight: bold;
}

.nav-bar a {
  text-decoration: none;
  color: white;
  margin: 0 8px;
  font-size: 16px;
  transition: color 0.2s;
}

.nav-bar a:hover {
  color: #00bcd4;
}

.nav-links {
  display: flex;
  align-items: center;
  gap: 16px;
  flex: 1;
  justify-content: center;
}

.navlink-container {
  display: flex;
  gap: 20px;
}

.activenav {
  border-bottom: 3px solid coral;
  border-radius: 1px;
  color: coral;
}

/* Search wrapper */
.nav-search-wrapper {
  flex: 0 1 280px;
  max-width: 280px;
  min-width: 200px;
}

.nav-search-form {
  position: relative;
  width: 100%;
}

.nav-search-input {
  width: 100%;
  padding: 8px 12px;
  border-radius: 20px;
  border: 1px solid #ccc;
  font-size: 14px;
  outline: none;
  box-sizing: border-box;
}

.nav-search-results {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  background: #fff;
  border: 1px solid #eee;
  border-radius: 0 0 8px 8px;
  z-index: 1000;
  max-height: 200px;
  overflow-y: auto;
  box-shadow: 0 2px 8px rgba(0,0,0,0.08);
  list-style: none;
  margin: 0;
  padding: 0;
}

.nav-search-result-item {
  padding: 10px 16px;
  cursor: pointer;
  font-size: 14px;
  color: #333;
}

.nav-search-result-item:hover {
  background: #f0f0f0;
}

/* Actions section */
.nav-actions {
  display: flex;
  align-items: center;
  gap: 12px;
  flex-shrink: 0;
}

.nav-cart button {
  width: 80px;
  height: 36px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 6px;
  background: none;
  color: white;
  border: 2px solid white;
  border-radius: 5px;
  cursor: pointer;
  transition: 0.2s all ease;
  font-size: 14px;
}

.nav-cart button:hover {
  transform: scale(0.95);
  border-color: #00bcd4;
}

.activecart {
  border-color: coral;
}

/* Profile dropdown */
.nav-profile {
  position: relative;
}

.profile-btn {
  background: none;
  border: none;
  cursor: pointer;
  padding: 6px;
  border-radius: 50%;
  transition: background 0.2s;
  color: white;
}

.profile-btn:hover {
  background: rgba(255, 255, 255, 0.1);
}

.profile-dropdown {
  position: absolute;
  top: 100%;
  right: 0;
  background: #222e3c;
  color: #fff;
  border: 2px solid #00bcd4;
  border-radius: 8px;
  box-shadow: 0 2px 12px rgba(0,0,0,0.18);
  min-width: 170px;
  z-index: 100;
  display: flex;
  flex-direction: column;
  margin-top: 8px;
}

.profile-dropdown button {
  background: none;
  border: none;
  text-align: left;
  padding: 12px 16px;
  cursor: pointer;
  font-size: 14px;
  color: #fff;
  display: flex;
  align-items: center;
  gap: 8px;
  transition: background 0.2s;
}

.profile-dropdown button:hover {
  background: #00bcd4;
  color: #222e3c;
}

/* Mobile hamburger menu */
.mobile-menu-toggle {
  display: none;
  background: none;
  border: none;
  color: white;
  font-size: 24px;
  cursor: pointer;
  padding: 8px;
}

.mobile-menu {
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  background: black;
  border-top: 1px solid #333;
  z-index: 1000;
  padding: 16px;
}

.mobile-menu.open {
  display: block;
}

.mobile-nav-links {
  display: flex;
  flex-direction: column;
  gap: 16px;
  margin-bottom: 16px;
}

.mobile-search {
  margin-bottom: 16px;
}

.mobile-actions {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* Responsive breakpoints */
@media (max-width: 1024px) {
  .nav-search-wrapper {
    flex: 0 1 220px;
    max-width: 220px;
    min-width: 180px;
  }
  
  .navlink-container {
    gap: 16px;
  }
  
  .nav-bar a {
    font-size: 15px;
  }
}

@media (max-width: 768px) {
  .nav-bar {
    padding: 8px 12px;
    flex-wrap: nowrap;
  }
  
  .nav-links,
  .nav-search-wrapper {
    display: none;
  }
  
  .mobile-menu-toggle {
    display: block;
    order: 2;
  }
  
  .nav-logo {
    order: 1;
  }
  
  .nav-actions {
    order: 3;
    gap: 8px;
  }
  
  .nav-cart button {
    width: 70px;
    height: 32px;
    font-size: 12px;
  }
  
  .nav-bar .nav-logo img {
    width: 50px;
  }
  
  .nav-bar .nav-logo p {
    font-size: 16px;
  }
  
  .profile-dropdown {
    right: -20px;
    min-width: 150px;
  }
}

@media (max-width: 480px) {
  .nav-bar {
    padding: 6px 8px;
    min-height: 50px;
  }
  
  .nav-cart button {
    width: 60px;
    height: 28px;
    font-size: 11px;
    padding: 4px;
  }
  
  .nav-bar .nav-logo img {
    width: 40px;
  }
  
  .nav-bar .nav-logo p {
    font-size: 14px;
  }
  
  .profile-dropdown {
    right: -30px;
    min-width: 140px;
  }
  
  .profile-dropdown button {
    padding: 10px 12px;
    font-size: 13px;
  }
}