.home-products-container {
  padding: 20px 16px;
  max-width: 1400px;
  margin: 0 auto;
}

.products {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 24px;
  justify-items: center;
  margin: 20px 0;
  padding: 0 16px;
}

.pagination {
  margin: 40px 0 20px 0;
  text-align: center;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 8px;
}

.pagination button {
  margin: 4px;
  padding: 8px 16px;
  border: none;
  background: #eee;
  cursor: pointer;
  border-radius: 6px;
  font-size: 14px;
  min-width: 40px;
  transition: all 0.2s ease;
}

.pagination button:hover {
  background: #ddd;
  transform: translateY(-1px);
}

.pagination button.active {
  background: #333;
  color: #fff;
}

/* Responsive breakpoints */
@media (max-width: 1200px) {
  .products {
    grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
    gap: 20px;
  }
}

@media (max-width: 768px) {
  .home-products-container {
    padding: 16px 8px;
  }
  
  .products {
    grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
    gap: 16px;
    padding: 0 8px;
  }
  
  .pagination {
    margin: 30px 0 15px 0;
  }
  
  .pagination button {
    padding: 6px 12px;
    font-size: 13px;
    min-width: 36px;
  }
}

@media (max-width: 480px) {
  .home-products-container {
    padding: 12px 4px;
  }
  
  .products {
    grid-template-columns: 1fr;
    gap: 12px;
    padding: 0 4px;
  }
  
  .pagination {
    margin: 20px 0 10px 0;
  }
  
  .pagination button {
    padding: 5px 10px;
    font-size: 12px;
    min-width: 32px;
    margin: 2px;
  }
}

/* Loading and empty states */
.loading-container,
.empty-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 300px;
  text-align: center;
}

.loading-container {
  flex-direction: column;
  gap: 16px;
}

.empty-container p {
  font-size: 18px;
  color: #666;
  margin: 0;
}

@media (max-width: 768px) {
  .empty-container p {
    font-size: 16px;
    padding: 0 16px;
  }
}