.cart-item {
  display: flex;
  background: white;
  border-radius: 12px;
  padding: 20px;
  margin-bottom: 16px;
  align-items: center;
  box-shadow: 0 2px 8px rgba(0,0,0,0.08);
  transition: all 0.3s ease;
  border: 1px solid #f0f0f0;
  gap: 16px;
}

.cart-item:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 16px rgba(0,0,0,0.12);
}

.cart-item .image {
  flex-shrink: 0;
  width: 80px;
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #f8f9fa;
  border-radius: 8px;
  overflow: hidden;
}

.cart-item .image img {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
}

.cart-details {
  flex: 1;
  min-width: 0;
}

.cart-details .title {
  font-size: 16px;
  font-weight: 600;
  color: #333;
  margin: 0 0 8px 0;
  line-height: 1.4;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.cart-details p {
  font-size: 14px;
  color: #666;
  margin: 0;
}

.price {
  display: flex;
  align-items: center;
  gap: 12px;
  flex-shrink: 0;
}

.price button {
  width: 36px;
  height: 36px;
  border-radius: 50%;
  border: 2px solid #e0e0e0;
  background: white;
  font-size: 18px;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #333;
}

.price button:hover {
  background: #667eea;
  border-color: #667eea;
  color: white;
  transform: scale(1.1);
}

.price button:disabled {
  background: #f5f5f5;
  border-color: #e0e0e0;
  color: #ccc;
  cursor: not-allowed;
  transform: none;
}

.price p {
  font-size: 16px;
  font-weight: 600;
  color: #e91e63;
  margin: 0;
  min-width: 80px;
  text-align: center;
}

.remove-item {
  flex-shrink: 0;
}

.remove-item button {
  padding: 8px 16px;
  border-radius: 8px;
  background: linear-gradient(135deg, #ff6b6b 0%, #ee5a52 100%);
  color: white;
  border: none;
  cursor: pointer;
  font-size: 14px;
  font-weight: 600;
  transition: all 0.3s ease;
}

.remove-item button:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(255, 107, 107, 0.4);
}

/* Responsive breakpoints */
@media (max-width: 768px) {
  .cart-item {
    padding: 16px;
    gap: 12px;
    flex-wrap: wrap;
  }
  
  .cart-item .image {
    width: 60px;
    height: 60px;
  }
  
  .cart-details .title {
    font-size: 15px;
  }
  
  .cart-details p {
    font-size: 13px;
  }
  
  .price {
    gap: 10px;
    order: 3;
    width: 100%;
    justify-content: center;
    margin-top: 12px;
  }
  
  .price button {
    width: 32px;
    height: 32px;
    font-size: 16px;
  }
  
  .price p {
    font-size: 15px;
    min-width: 70px;
  }
  
  .remove-item {
    order: 4;
    width: 100%;
    text-align: center;
    margin-top: 8px;
  }
  
  .remove-item button {
    width: 100%;
    max-width: 200px;
    padding: 10px 16px;
  }
}

@media (max-width: 480px) {
  .cart-item {
    padding: 12px;
    gap: 10px;
  }
  
  .cart-item .image {
    width: 50px;
    height: 50px;
  }
  
  .cart-details .title {
    font-size: 14px;
    -webkit-line-clamp: 1;
  }
  
  .cart-details p {
    font-size: 12px;
  }
  
  .price {
    gap: 8px;
    margin-top: 10px;
  }
  
  .price button {
    width: 30px;
    height: 30px;
    font-size: 14px;
  }
  
  .price p {
    font-size: 14px;
    min-width: 60px;
  }
  
  .remove-item button {
    padding: 8px 14px;
    font-size: 13px;
  }
}

/* Animation for quantity changes */
.quantity-change {
  animation: pulse 0.3s ease;
}

@keyframes pulse {
  0% { transform: scale(1); }
  50% { transform: scale(1.1); }
  100% { transform: scale(1); }
}