.product-details-container {
  display: flex;
  flex-direction: row;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 40px 20px;
  gap: 40px;
  align-items: flex-start;
  min-height: calc(100vh - 120px);
}

.left-container {
  flex: 1;
  max-width: 500px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #f8f9fa;
  border-radius: 16px;
  padding: 40px;
  box-shadow: 0 4px 20px rgba(0,0,0,0.08);
}

.product-detail-image {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.product-detail-image img {
  max-width: 100%;
  max-height: 400px;
  object-fit: contain;
  border-radius: 12px;
  transition: transform 0.3s ease;
}

.product-detail-image img:hover {
  transform: scale(1.05);
}

.right-container {
  flex: 1;
  max-width: 600px;
  padding: 20px 0;
}

.product-details-text {
  margin-bottom: 40px;
}

.product-details-text h2 {
  font-size: 28px;
  font-weight: 700;
  color: #333;
  margin: 0 0 16px 0;
  line-height: 1.3;
}

.product-details-text p {
  font-size: 16px;
  color: #666;
  line-height: 1.6;
  margin: 12px 0;
}

.product-details-text p:first-of-type {
  font-size: 14px;
  color: #999;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-weight: 600;
}

.product-details-text h3 {
  font-size: 32px;
  font-weight: 700;
  color: #e91e63;
  margin: 24px 0;
}

.product-details-buttons {
  display: flex;
  gap: 16px;
  flex-wrap: wrap;
}

.product-details-buttons button {
  padding: 16px 32px;
  cursor: pointer;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border: none;
  border-radius: 12px;
  font-size: 16px;
  font-weight: 600;
  transition: all 0.3s ease;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  min-width: 160px;
}

.product-details-buttons button:hover {
  transform: translateY(-3px);
  box-shadow: 0 8px 25px rgba(102, 126, 234, 0.4);
}

.add-popup {
  background: linear-gradient(135deg, #4caf50 0%, #45a049 100%);
  color: white;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 60px;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
  font-size: 16px;
  font-weight: 600;
  box-shadow: 0 2px 12px rgba(76, 175, 80, 0.3);
  animation: slideDown 0.3s ease;
}

@keyframes slideDown {
  from {
    transform: translateY(-100%);
  }
  to {
    transform: translateY(0);
  }
}

/* Responsive breakpoints */
@media (max-width: 1024px) {
  .product-details-container {
    padding: 30px 16px;
    gap: 30px;
  }
  
  .left-container {
    padding: 30px;
  }
  
  .product-details-text h2 {
    font-size: 24px;
  }
  
  .product-details-text h3 {
    font-size: 28px;
  }
}

@media (max-width: 768px) {
  .product-details-container {
    flex-direction: column;
    padding: 20px 12px;
    gap: 24px;
  }
  
  .left-container {
    max-width: 100%;
    padding: 24px;
  }
  
  .product-detail-image img {
    max-height: 300px;
  }
  
  .right-container {
    max-width: 100%;
    padding: 0;
  }
  
  .product-details-text h2 {
    font-size: 22px;
  }
  
  .product-details-text p {
    font-size: 15px;
  }
  
  .product-details-text h3 {
    font-size: 26px;
  }
  
  .product-details-buttons {
    justify-content: center;
  }
  
  .product-details-buttons button {
    padding: 14px 28px;
    font-size: 15px;
    min-width: 140px;
  }
  
  .add-popup {
    height: 50px;
    font-size: 15px;
  }
}

@media (max-width: 480px) {
  .product-details-container {
    padding: 16px 8px;
    gap: 20px;
  }
  
  .left-container {
    padding: 20px;
  }
  
  .product-detail-image img {
    max-height: 250px;
  }
  
  .product-details-text h2 {
    font-size: 20px;
  }
  
  .product-details-text p {
    font-size: 14px;
  }
  
  .product-details-text h3 {
    font-size: 24px;
    margin: 20px 0;
  }
  
  .product-details-buttons {
    flex-direction: column;
    align-items: center;
  }
  
  .product-details-buttons button {
    width: 100%;
    max-width: 280px;
    padding: 12px 24px;
    font-size: 14px;
  }
  
  .add-popup {
    height: 45px;
    font-size: 14px;
  }
}

/* Loading skeleton */
.product-details-skeleton {
  background: linear-gradient(90deg, #f0f0f0 25%, #e0e0e0 50%, #f0f0f0 75%);
  background-size: 200% 100%;
  animation: loading 1.5s infinite;
  border-radius: 8px;
}

@keyframes loading {
  0% {
    background-position: 200% 0;
  }
  100% {
    background-position: -200% 0;
  }
}