.product-header {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin: 30px 0;
  padding: 0 16px;
}

.product-heading {
  margin-bottom: 24px;
}

.product-heading h1 {
  font-size: 36px;
  font-weight: 700;
  color: #333;
  margin: 0;
  text-align: center;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.product-filter {
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
  justify-content: center;
  align-items: center;
}

.filter-btn {
  padding: 10px 20px;
  background: white;
  border: 2px solid #e0e0e0;
  border-radius: 25px;
  font-size: 14px;
  font-weight: 600;
  transition: all 0.3s ease;
  cursor: pointer;
  color: #333;
  text-transform: capitalize;
  white-space: nowrap;
}

.filter-btn:hover {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border-color: transparent;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(102, 126, 234, 0.3);
}

.filter-btn:active {
  transform: translateY(0);
}

.filter-btn.active {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border-color: transparent;
  box-shadow: 0 2px 8px rgba(102, 126, 234, 0.3);
}

/* Responsive breakpoints */
@media (max-width: 768px) {
  .product-header {
    margin: 24px 0;
    padding: 0 12px;
  }
  
  .product-heading {
    margin-bottom: 20px;
  }
  
  .product-heading h1 {
    font-size: 28px;
  }
  
  .product-filter {
    gap: 10px;
  }
  
  .filter-btn {
    padding: 8px 16px;
    font-size: 13px;
  }
}

@media (max-width: 480px) {
  .product-header {
    margin: 20px 0;
    padding: 0 8px;
  }
  
  .product-heading {
    margin-bottom: 16px;
  }
  
  .product-heading h1 {
    font-size: 24px;
  }
  
  .product-filter {
    gap: 8px;
    max-width: 100%;
  }
  
  .filter-btn {
    padding: 6px 14px;
    font-size: 12px;
    flex: 1;
    min-width: 0;
    text-align: center;
  }
  
  /* Stack filters in rows on very small screens */
  .product-filter {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 8px;
    width: 100%;
  }
}

@media (max-width: 360px) {
  .filter-btn {
    padding: 6px 10px;
    font-size: 11px;
  }
  
  .product-heading h1 {
    font-size: 22px;
  }
}

/* Animation for filter changes */
.filter-btn {
  position: relative;
  overflow: hidden;
}

.filter-btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255,255,255,0.2), transparent);
  transition: left 0.5s;
}

.filter-btn:hover::before {
  left: 100%;
}

/* Focus states for accessibility */
.filter-btn:focus {
  outline: 2px solid #007bff;
  outline-offset: 2px;
}