.footer-container {
  width: 100%;
  min-height: 200px;
  background: linear-gradient(135deg, #2c3e50 0%, #34495e 100%);
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 40px 20px;
  margin-top: 60px;
}

.footer-content {
  max-width: 1200px;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 40px;
  text-align: center;
}

.footer-section h3 {
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 16px;
  color: #ecf0f1;
}

.footer-section p,
.footer-section a {
  font-size: 14px;
  color: #bdc3c7;
  text-decoration: none;
  line-height: 1.6;
  margin: 8px 0;
  transition: color 0.3s ease;
}

.footer-section a:hover {
  color: #3498db;
}

.footer-bottom {
  margin-top: 30px;
  padding-top: 20px;
  border-top: 1px solid #34495e;
  text-align: center;
  width: 100%;
}

.footer-bottom p {
  font-size: 14px;
  color: #95a5a6;
  margin: 0;
}

/* Responsive breakpoints */
@media (max-width: 768px) {
  .footer-container {
    padding: 30px 16px;
    min-height: 150px;
  }
  
  .footer-content {
    grid-template-columns: 1fr;
    gap: 24px;
  }
  
  .footer-section h3 {
    font-size: 18px;
    margin-bottom: 12px;
  }
  
  .footer-section p,
  .footer-section a {
    font-size: 13px;
  }
  
  .footer-bottom {
    margin-top: 20px;
    padding-top: 16px;
  }
  
  .footer-bottom p {
    font-size: 13px;
  }
}

@media (max-width: 480px) {
  .footer-container {
    padding: 24px 12px;
    min-height: 120px;
  }
  
  .footer-content {
    gap: 20px;
  }
  
  .footer-section h3 {
    font-size: 16px;
    margin-bottom: 10px;
  }
  
  .footer-section p,
  .footer-section a {
    font-size: 12px;
    margin: 6px 0;
  }
  
  .footer-bottom {
    margin-top: 16px;
    padding-top: 12px;
  }
  
  .footer-bottom p {
    font-size: 12px;
  }
}